import React, { useState } from "react";
import ReactPlayer from "react-player";
import Typing from "react-typing-animation";
import "./Video.css";
const Video = () => {
  const [isVideoReady, setIsVideoReady] = useState(false);
  return (
    <div
      style={{
        position: "relative",
        // height: "auto"
        height: "calc(100vw/1.8)"
      }}
      className={"bg-3"}
    >
      <div
        // className="video-background-animation"
        style={{
          position: "absolute",

          height: "100%",
          backgroundColor: "#000000bb",

          width: "100%",
          zIndex: 1,
          display: "flex",
          justifyContent: "center",
          alignItems: "center"
        }}
      >
        <div
          style={
            {
              // "box-shadow": "rgba(0, 0, 0, 0.5) 0px 0px 17px 15px",
              // width: "80%",
              // padding: "50px",
              // borderRadius: "15px"
            }
          }
        >
          <h2
            className="display-4"
            style={{ color: "rgb(216, 27, 67)", textAlign: "center" }}
          >
            WOODSTOCK
          </h2>
          <Typing
            speed={100}
            loop={true}
            cursor={<b style={{ color: "#eef2f5" }}>|</b>}
          >
            <h1 style={{ color: "#eeeeee", textAlign: "center" }}>
              {/* <hr style={{ border: "2px solid rgb(245, 88, 123)" }} /> */}
              Welcome To Our Coffe Shop
              {/* <Typing.Backspace count={11} delay={5000} speed={100} /> */}
            </h1>
            <Typing.Reset count={1} delay={5000} />
          </Typing>
        </div>
      </div>
      <ReactPlayer
        onBufferEnd={() => {
          setIsVideoReady(true);
        }}
        width={"100%"}
        height={isVideoReady ? "auto" : "0px"}
        url="video/coffee5.mp4"
        playing
        loop={true}
        muted={true}
        controls={false}
        config={{
          youtube: {
            playerVars: { showinfo: 0, rel: 0 },
            embedOptions: { showinfo: 0, rel: 0 }
          }
        }}

        // controls
      />
    </div>
  );
};

export default Video;
