import React from "react";
import List from "./List/List";
const Gallery = () => {
  return (
    <div style={{ padding: "20px 0 0 0" }}>
      <h1 style={{ textAlign: "center", color: "rgb(216, 27, 67)" }}>
        Our Gallery
      </h1>
      <hr
        style={{
          backgroundColor: "rgb(208, 50, 83)",
          height: "2px",
          width: "20%",
          marginBottom: "30px"
        }}
      />

      <List
        images={[
          "img/1.jpg",
          "img/2.jpg",
          "img/3.jpg",
          "img/4.jpg",
          "img/5.jpg",
          "img/6.jpg",
          "img/7.jpg"
        ]}
      />
    </div>
  );
};

export default Gallery;
