import React from "react";
import CopyRight from "./CopyRight/CopyRight";
import { Container, Row, Col } from "react-bootstrap";
import facebookIcon from "./CopyRight/icons/facebook.png";
import "./Footer.css";
const Footer = () => {
  return (
    <>
      <Container fluid={true} style={{ padding: 0 }}>
        <Row
          className="bg-footer"
          style={{ position: "relative" }}
          noGutters={true}
          style={{ padding: "20px 0 40px 0" }}
        >
          <div
            style={{
              position: "absolute",
              width: "100%",
              height: "100%",
              top: 0,
              left: 0,
              backgroundColor: "#000000ba"
            }}
          ></div>

          <Col
            xs={12}
            sm={6}
            style={{
              textAlign: "center",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              paddingTop: "18px"
            }}
          >
            <div
              style={{
                minHeight: "300px",
                width: "266px"
                // boxShadow: "rgba(0, 0, 0, 0.5) 0px 0px 17px 15px"
              }}
            >
              <h2
                style={{
                  //  fontFamily: "Roboto Condensed",
                  marginTop: "50px",
                  color: "rgb(216, 27, 67)"
                }}
              >
                Opening Hours
              </h2>
              <hr
                style={{
                  backgroundColor: "rgb(208, 50, 83)",
                  height: "2px",
                  width: "50%",
                  marginBottom: "30px"
                }}
              />
              <Row
                style={{ color: "#eee", fontSize: "20px", lineHeight: "30px" }}
              >
                <Col xs={6}>
                  <div>Monday :</div>
                  <div>Tuesday :</div>
                  <div>Wednesday :</div>
                  <div>Thursday :</div>
                  <div>Friday :</div>
                  <div>Saturday :</div>
                  <div>Sunday :</div>
                </Col>
                <Col xs={6}>
                  <div>6:00 – 23:30</div>
                  <div>6:00 – 23:30</div>
                  <div>6:00 – 23:30</div>
                  <div>6:00 – 23:30</div>
                  <div>6:00 – 23:30</div>
                  <div>6:00 – 23:30</div>
                  <div>6:00 – 23:30</div>
                </Col>
              </Row>
            </div>
          </Col>
          <Col
            xs={12}
            sm={6}
            style={{
              textAlign: "center",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              paddingTop: "18px"
            }}
          >
            <div
              style={{
                minHeight: "300px",
                paddingBottom: "45px"
              }}
            >
              <h2
                style={{
                  //  fontFamily: "Roboto Condensed",
                  marginTop: "50px",
                  color: "rgb(216, 27, 67)"
                }}
              >
                Our Social Media
              </h2>
              <hr
                style={{
                  backgroundColor: "rgb(208, 50, 83)",
                  height: "2px",
                  width: "50%",
                  marginBottom: "30px"
                }}
              />
              <a
                href="https://www.facebook.com/WoodStock-Le-Bardo-477415192741920"
                target="_blank"
              >
                <img
                  src={facebookIcon}
                  style={{ width: "65px", height: "65px" }}
                />
              </a>
            </div>
          </Col>
        </Row>
      </Container>

      <CopyRight />
    </>
  );
};

export default Footer;
