import React, { Component } from "react";
import OlMap from "ol/Map";
import { fromLonLat, toLonLat } from "ol/proj";
import OlView from "ol/View";
import OlLayerTile from "ol/layer/Tile";
import OlSourceOSM from "ol/source/OSM";
import Overlay from "ol/Overlay";

import "ol/ol.css";
class Map1 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lotLat: []
    };
    this.container = React.createRef();
  }
  componentDidMount() {
    //create an overlayer
    const overlay = new Overlay({
      stopEvent: true,
      positioning: "center-center",
      position: fromLonLat([10.1472, 36.8087]),
      element: this.container.current
    });

    this.overL = overlay;

    this.olmap = new OlMap({
      target: "map-id",
      layers: [
        new OlLayerTile({
          source: new OlSourceOSM()
        })
      ],
      overlays: [overlay],

      view: new OlView({
        center: fromLonLat([10.1472, 36.8087]),
        zoom: 17
      })
    });
  }

  render() {
    return (
      <>
        <div id="map-id" style={{ width: "100%" }} />
        <div
          ref={this.container}
          draggable={false}
          style={{ position: "relative", width: "1px", height: "1px" }}
        >
          <div
            style={{
              top: "3px",
              position: "absolute",
              left: "-38px",
              color: "rgb(208, 50, 83)"
            }}
          >
            woodStock
          </div>
          <img
            draggable={false}
            style={{
              // cursor: "grab",
              position: "absolute",
              right: "-18px",
              bottom: "-6px",
              width: "32px"
            }}
            src="marker.png"
          />
        </div>
      </>
    );
  }
}

export default Map1;
